/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Voracious Bookworm","author":"Lei"}},"markdownRemark":{"id":"/home/lei-lo/Code/voraciouscoder.github.io/src/pages/reading-roundup-32/index.md absPath of file >>> MarkdownRemark","html":"<p>I’m in that “last stretch of a book challenge” frenzy (imagine the FINISH HIM! cry in Mortal Kombat ;) ). It’s to the point where I have the same book on hold at two libraries 🙊 And anxiously creeping on what people are reading on the subway (it’s for a reading challenge, okay?). I’m almost there though!</p>\n<p><strong>James and the Giant Peach</strong><br>\n<em>Popsugar Reading Challenge: A book that is also a stage play or musical</em></p>\n<p>I struggled with this challenge because it was mostly classic novels on the lists I found. Not super appealing to me, so I chose the throw it back to a play I remember being in as a kid! I don’t remember which character I was though 🤔</p>\n<p>It’s funny how many details I’d forgotten about the book - James’ aunts, the seagulls, and the Gloworm. Definitely worth the re-read of one of my fave children’s book authors. Maybe next up is Matilda!</p>\n<p><strong>Chocolat</strong><br>\n<em>Popsugar Reading Challenge: A book made into a movie that you’ve already seen</em></p>\n<p>Another tougher challenge for me, since I don’t watch a lot of movies. I had to read a bunch of lists to find one that I felt like experiencing the story again (albeit in a different way).</p>\n<p>Watching the movie first ruined this book for me. I kept looking for details from the movie! It mostly made me want to watch the movie again.</p>\n<p>The book did have mouthwatering descriptions of food and desserts though. I also enjoyed the extra details on Armande the grandmother. Overall a fun read - but probably better if you read it first.</p>\n<p><strong>The Bat</strong><br>\n<em>Popsugar Reading Challenge: Nordic noir</em></p>\n<p>I hated the callous treatment of the love interest in this story. It just wasn’t believable to me that someone would agree to be serial killer bait. He might as well have <a href=\"https://en.wikipedia.org/wiki/Women_in_Refrigerators\">put her in a refrigerator</a> 🙄 Ugh.</p>\n<p>Otherwise, if you’re expecting a story set in Norway, this is not the one. There are a lot of twists and turns but it’s all set in Australia. Similar to Louise Penny’s debut <em>Still Life</em>, I can tell that this is Nesbo’s best work but it’s fun to see the beginning!</p>\n<p><strong>Modern Romance</strong><br>\n<em>Popsugar Reading Challenge: A past Goodreads Choice Awards winner</em></p>\n<p>Meh. If you’ve already read Helen Fisher, Esther Perel, and Sheena Iyengar, this book isn’t much of a revelation. The best parts were the anecdotes from the focus groups (that were part of the research of this book) and the insights into the culture of romance in Japan.</p>\n<p>It <em>was</em> interesting to read from a man’s perspective, especially given that Ansari is wont to give examples from his own life. I’ve mostly read and listened to women on the subject, so this was new for me.</p>\n<p>I think I’ll go back to listening to mythologies though - it’s more apt for storytelling out loud, and I’m not missing any graphs.</p>","frontmatter":{"title":"Reading Roundup 32","date":"October 14, 2018"}}},"pathContext":{"slug":"/reading-roundup-32/","previous":{"fields":{"slug":"/reading-roundup-31/"},"frontmatter":{"title":"Reading Roundup 31"}},"next":{"fields":{"slug":"/reading-roundup-33/"},"frontmatter":{"title":"Reading Roundup 33"}}}}

/***/ })

